  border: 3px solid #4D774E;
}
.single-recipe h2 {
  text-align: center;
}

.single-recipe__intro {
  margin: 3% 0;
  display: flex;
  justify-content: space-evenly;
  font-size: 1.2rem;
}
.single-recipe__intro h3 {
