}

.info__time {
  height: 50px;
  background-image: url("https://www.dropbox.com/s/9ik07gru886kb1y/time.svg?raw=1");
  background-size: 50px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.info__time p {
  margin-left: 60px;
}

.info__cuisine {
  height: 50px;
  background-image: url("https://www.dropbox.com/s/ixzcamugskofpzs/cuisine.svg?raw=1");
  background-size: 50px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.info__cuisine p {
  margin-left: 60px;
}

.info__diet {
  height: 50px;
  background-image: url("https://www.dropbox.com/s/f2rdg4lyp8n2wa2/diet.svg?raw=1");
  background-size: 50px;
  background-repeat: no-repeat;
  background-position: 0 0;
}
.info__diet p {
  margin-left: 60px;
}

.single-recipe__description {
  text-align: center;
  padding: 2%;
