}
.single-recipe__description q {
  font-size: 1.2rem;
}

.single-recipe__image {
  width: 45%;
  transition: all 0.45s;
}
.single-recipe__image img {
  width: 100%;
  border-radius: 4px;
  box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.25);
  transition: all 0.45s;
}
.single-recipe__image img:hover {
  cursor: zoom-in;
}

